button {
  cursor: pointer;
}

button:hover {
  opacity: 0.9;
  transform: scale(1.02);
  transition: all 0.2s ease-in-out;
}

.button_contact {
  border: none;
  background-color: transparent;
}

.button_telegram {
  background-image: url("../images/icon_type_telegram.svg");
  background-repeat: no-repeat;
  background-size: cover;
}

.button_viber {
  background-image: url("../images/icon_type_viber.svg");
  background-repeat: no-repeat;
  background-size: cover;
}

.button_whatsapp {
  background-image: url("../images/icon_type_whatsapp.svg");
  background-repeat: no-repeat;
  background-size: cover;
}

.button_telegram_desktop {
  width: 16.68px;
  height: 13.99px;
  margin-right: 33.65px;
}

.button_viber_desktop {
  width: 16px;
  height: 15.2px;
  margin-right: 34px;
}

.button_whatsapp_desktop {
  width: 16px;
  height: 16px;
}

.button_telegram_mobile {
  width: 20.02px;
  height: 16.79px;
  margin-right: 13.98px;
}

.button_viber_mobile {
  width: 20px;
  height: 19px;
  margin-right: 14.4px;
}

.button_whatsapp_mobile {
  width: 19.2px;
  height: 19.2px;
}

.button_place_main {
  display: none;
  background-color: #4077f3;
  border: none;
  color: #e4e5ea;
  font-style: Medium;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  margin-top: 60px;
  align-self: end;
}

.button_size_xl {
  width: 262px;
  height: 61px;
}

.button_address {
  background-color: transparent;
  border: none;
  width: max-content;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  padding: 0;
}

.button_address:hover {
  text-decoration: underline;
  opacity: 1;
  transform: none;
}

.button_telegram_footer {
  margin-right: 13.98px;
}

.button_viber_footer {
  margin-right: 14.4px;
}

@media (max-width: 500px) {
  .button_address {
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
  }
}

@media (min-width: 500px) {
  .button_place_main {
    display: block;
  }
}

@media (max-width: 1140px) {
  .button_whatsapp_desktop {
    margin-right: 16px;
  }

  .button_place_main {
    margin-right: 16px;
  }
}
